export class FileExportTsqlTableViewModel {
  /// <summary>
  ///  轉檔序號
  /// <summary>
  public exec_file_seq: number;
  /// <summary>
  ///  結構描述
  /// <summary>
  public schemaname: string;
  /// <summary>
  ///  資料表
  /// <summary>
  public tablename: string;
  /// <summary>
  ///  db連線編號
  /// <summary>
  public db_conn_id: string;
  /// <summary>
  ///  參數欄位群組
  /// <summary>
  public parameter_column_group: string;
  /// <summary>
  ///  檔案名稱
  /// <summary>
  public filename: string;
  /// <summary>
  ///  匯出模式主鍵
  /// <summary>
  public export_mode_key: string;

  /// <summary>
  /// Sql語法
  /// <summary>
  public sql_statement: string;
  /// <summary>
  ///  檔案內文格式主鍵
  /// <summary>
  public file_format_key: string;
  /// <summary>
  ///  欄位分隔符號(f:表固定長度,\t:tab,\b:char(8),\0:char(0),f|:固定長度後加上|)
  /// <summary>
  public field_terminator: string;
  /// <summary>
  ///  列結尾符號
  /// <summary>
  public row_terminator: string;
  /// <summary>
  ///  啟用註記
  /// <summary>
  public is_active: boolean;
  /// <summary>
  ///  執行順序
  /// <summary>
  public exec_seq: number;
  /// <summary>
  ///  最後資料型態主鍵
  /// <summary>
  public last_data_type_key: string;
  /// <summary>
  ///  最後資料欄位名稱
  /// <summary>
  public last_data_column_name: string;
  /// <summary>
  ///  查詢資料條件
  /// <summary>
  public where_condition: string;
  /// <summary>
  ///  空檔刪除
  /// <summary>
  public del_empty_file: Boolean;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;

  public is_active_desc: string;
  /// <summary>
  ///  啟用註記顏色
  /// <summary>
  public is_active_color: string;
  /// <summary>
  ///  啟用顏色
  /// <summary>
  public is_active_color_right: string;
  /// <summary>
  ///  匯出模式
  /// <summary>
  public export_mode: string;
  /// <summary>
  ///  資料表全名
  /// <summary>
  public tablefullname: string;
  /// <summary>
  ///  db連線字串
  /// <summary>
  public db_conn_string: string;
  /// <summary>
  ///  檔案內文格式
  /// <summary>
  public file_format: string;

  /// <summary>
  ///  最後資料型態
  /// <summary>
  public last_data_type: string;

  /// <summary>
  ///  是否有設定執行後SQL
  /// <summary>
  public is_exported_sql: Boolean;

  /// <summary>
  /// exportedSql顏色
  /// </summary>
  public exported_sql_color: string;

  /// <summary>
  ///  是否有設定檔頭
  /// <summary>
  public is_exported_header: Boolean;

  /// <summary>
  ///  是否有設定檔頭顏色
  /// </summary>
  public exported_header_color: string;
  /// <summary>
  ///  資料來源匯出類型主鍵
  /// <summary>
  public src_export_type_key: string;
  /// <summary>
  ///  資料來源匯出類型
  /// <summary>
  public src_export_type: string;
}



